<template>
  <div>
    <h2 class="page-head">Salary Increment</h2>
    <div class="main">
      <hr />
      <b-row class="mt-2">
        <b-col cols="2" class="col-left">
          <label for="input-code">Trn. Id:</label>
        </b-col>
        <b-col cols="2" class="col-left">{{ salaryIncrement.trnId }}</b-col>
      </b-row>
      <b-row class="mt-1">
        <b-col cols="2" class="col-left">
          <label for="input-code">Code:</label>
        </b-col>
        <b-col cols="1">
          <b-form-group>
            <b-input-group>
              <b-form-input
                class="col-right form-control form-control-sm"
                v-model="salaryIncrement.empCode"
                placeholder="Employee code"
                @change="getEmployee"
                v-b-tooltip.hover
                title="Enter employee code"
                tabindex="1"
              ></b-form-input>
              <template v-slot:append>
                <searchSm
                  :column1Key="searchEmp.attr1.name"
                  :column1label="searchEmp.attr1.label"
                  :column2Key="searchEmp.attr2.name"
                  :column2label="searchEmp.attr2.label"
                  :fetchItem="fetchEmployeeT"
                  @fetchD="fetchEmployeeList"
                  :items="employeeList"
                  :reset="houseKeeping"
                  :totalRowsT="totalRows"
                />
              </template>
            </b-input-group>
          </b-form-group>
        </b-col>
        <b-col cols="3" class="col-left">{{ salaryIncrement.empName }}</b-col>
      </b-row>
      <b-row class="mt-1">
        <b-col cols="2" class="col-left">
          <label for="input-drAmount">Amount:</label>
        </b-col>
        <b-col cols="1">
          <template>
            <vue-numeric
              class="form-control form-control-sm styleLine col-right"
              style="padding-left: 0px"
              tabindex="2"
              placeholder
              separator=","
              :precision="3"
              v-model="salaryIncrement.salIncrement"
            ></vue-numeric>
          </template>
        </b-col>
      </b-row>
      <b-row class="mt-3">
        <b-col cols="2" class="col-left">
          <label for="input-description">Remarks:</label>
        </b-col>
        <b-col cols="6" class="col-left">
          <b-form-input
            id="input-description"
            v-model="salaryIncrement.remarks"
            tabindex="3"
          ></b-form-input>
        </b-col>
      </b-row>
      <b-row class="mt-3">
        <b-col cols="1" offset="2">
          <b-button variant="success" class="mr-1" @click="save()"
          tabindex="4"
            >Submit</b-button
          >
        </b-col>
        <b-col cols="1">
          <b-button
            variant="outline-primary"
            class="mr-1"
            @click="close()"
            tabindex="5"
          >
            Clear
          </b-button>
        </b-col>
      </b-row>
      <br />
    </div>
  </div>
</template>

<script>
import searchSm from '../components/SearchSm.vue'
import { mapActions, mapGetters } from 'vuex'
import axios from 'axios'
export default {
  data () {
    return {
      msg: {},
      errors: [],
      salaryIncrement: {
        empCode: 0,
        empName: '',
        salIncrement: 0,
        remarks: '',
        transactionDate: new Date()
      },
      searchEmp: {
        attr1: { name: 'id', label: 'Employee Code' },
        attr2: { name: 'empName', label: 'Employee Name' }
      }
    }
  },
  components: {
    searchSm
  },
  computed: {
    ...mapGetters(['employeeList', 'employee', 'fetchStartRec', 'totalRows'])
  },
  methods: {
    ...mapActions(['fetchEmployeeList', 'fetchEmployee']),
    houseKeeping () {
      this.salaryIncrement = {
        empCode: 0,
        empName: '',
        amount: 0,
        remarks: '',
        transactionDate: new Date()
      }
    },
    async fetchEmployeeT (x) {
      await this.fetchEmployee(x)
      this.salaryIncrement.empCode = this.employee.id
      this.salaryIncrement.empName = this.employee.empName
    },
    async getEmployee () {
      await this.fetchEmployee(this.salaryIncrement.empCode)
      if (this.message.length !== 0) {
        this.$toasted.show(this.message, {
          theme: 'bubble',
          position: 'top-right',
          duration: 5000
        })
      }
    },
    checkForm: function (e) {
      // console.log('checking form data')
      this.errors = []
      if (!this.salaryIncrement.empCode) {
        this.errors.push('Employee code.')
      }
      if (!this.salaryIncrement.salIncrement) {
        this.errors.push('Amount Required.')
      }
      if (!this.salaryIncrement.remarks) {
        this.errors.push('remarks Required.')
      }
      if (!this.errors.length) {
        return true
      } else {
        this.errors.unshift(
          'Fields highlighted in yellow are required, please enter following ..'
        )
      }
    },
    async save () {
      this.checkForm()
      if (!this.errors.length) {
        await this.saveSalaryIncrement(this.salaryIncrement)
        this.$toasted.show(
          this.message,
          {
            theme: 'bubble',
            position: 'top-right',
            duration: 5000
          })
        this.houseKeeping()
      } else {
        console.log('Show errors!!')
        this.$toasted.show(this.errors, {
          theme: 'bubble',
          position: 'top-right',
          duration: 5000
        })
      }
    },
    async saveSalaryIncrement (data) {
      // console.log('saving salary increment')
      const url = process.env.VUE_APP_API_URL + '/salaryIncrement/save'
      await axios
        .post(url, data, {
          headers: {
            'Content-Type': 'application/json'
          }
        })
        .then(response => {
          this.msg = {}
          //   commit('setMessage', 'Sucess')
          this.$toasted.show('Salary Increment Posted!', {
            theme: 'bubble',
            position: 'top-right',
            duration: 5000
          })
        })
        .catch(error => {
          console.log(error.response.data)
          this.msg = 'Error:' + JSON.stringify(error.response.data.errorMessage)
        })
    },
    close () {
      this.houseKeeping()
    }
  }
}
</script>

<style>
.main {
  width: 95%;
  margin-left: 40px;
  margin-right: 40px;
  /* font-family: "Arial", Times, serif; */
  font-size: small;
}
.col-left {
  text-align: left;
}
.col-right {
  text-align: right;
}
.col-middle {
  /* vertical-align: bottom; */
  /* padding-top: 25px;
  padding-left: 0px; */
  width: 50%;
}
.text-info {
  color: #007bff !important;
}
.text-primary {
  color: #fff;
  background-color: #343a40;
}
</style>
